import React from 'react'
import SignUpForm from './SignUpForm'
import vinyl from '../../../assets/Vinyl.svg'
import krIcon from '../../../assets/money.svg'
import Footer from '../../../components/common/Footer'
/*animation stuff*/
import QueueAnim from 'rc-queue-anim';

export default React.createClass({
  themeColor:"#B031FF",
  secondColor:"#31DAFF",


  render() {
    return  <div >
      <header className=""

      >
      <div id="stripes">
        <span/>
        <span/>
        <span className="white"/>
        <span/>
      </div>
      <div className="container">
        <div className="row">
        <QueueAnim type="top">
          <div key="cardA">
          <div  className="col-md-6">
            <div className="card glass">
              <img src={krIcon} alt="Money icon"/>
              <h2>Membership free of charge</h2>
              <p>
                Stripe builds the most powerful and flexible tools for internet commerce. Whether youre creating a subscription service, an on-demand marketplace, an e-commerce store, or a crowdfunding platform, Stripes meticulously-designed APIs and unmatched functionality help you create the best possible product for your users. Hundreds of thousands of the worlds
              </p>
            </div>
          </div>
        </div>
        <div key="cardB">
        <div  className="col-md-6">
          <div className="card glass">
            <img src={vinyl} alt="vinyl icon"/>
            <h2>Get the gigs you want to play</h2>
            <p>
              Stripe builds the most powerful and flexible tools for internet commerce. Whether youre creating a subscription service, an on-demand marketplace, an e-commerce store, or a crowdfunding platform, Stripes meticulously-designed APIs and unmatched functionality help you create the best possible product for your users. Hundreds of thousands of the worlds
            </p>
          </div>
        </div>
        </div>
      </QueueAnim>
        </div>
      </div>

    </header>
    <div className="container">
      <div className="card elevate signup">
        <SignUpForm/>
      </div>
    </div>
      <Footer
        color={this.themeColor}
        firstTo="/howitworks"
        secondTo="/"
        firstLabel="HOW IT WORKS"
        secondLabel="ARRANGE EVENT"
        title="Wonder how it works?"
        subTitle="See how it works, or arrange an event."
        />
    </div>
  }
})



